import * as React from 'react'
import { Link } from 'gatsby'
import { Button } from 'antd'
import { GithubOutlined, TwitterOutlined } from '@ant-design/icons'

const IndexPage = () => {
  return (
    <div align="center" style={{ padding: 80 }}>
      <p
        style={{
          color: '#663299',
          fontSize: 50,
          fontWeight: 'bold',
        }}
      >
        Gatsby
      </p>
      <h2>Projet de veille technologique réalisé par Noa Ammirati - IUT Informatique de Bordeaux DAWIN 2022</h2>
      <p>
        Ce site web s'appuie sur le starter template Gatsby de {' '}
        <a href="https://github.com/jannikbuschke/gatsby-antd-docs">
          https://github.com/jannikbuschke/gatsby-antd-docs
        </a>
      </p>
      <br />
      <Button.Group size="large">
        <Button type="primary" size="large">
          <Link to="/docs/template/get-started/introduction">Démarrer</Link>
        </Button>
      </Button.Group>
    </div>
  )
}

export default IndexPage
